
.nav {
  width: 1060px;
  height: 50px;
  line-height: 50px;
  margin: 0 auto;
}
.myPic {
  width: 40px;
  height: 40px;
  line-height: 39px;
  margin-top: 4px;
  overflow: hidden;
  border-radius: 20px;
  float: left;
}
.navbar li a {
  display: inline-block;
  width: 100%;
  height: 50px;
  font-weight: 700;
}
.navbar li a:hover {
  font-weight: 700;
  transition: all 300ms ease-out;
}
.active {
  background-color: #e3e3e3;
}
.nav ul li {
  display: inline-block;
  font-size: 18px;
  text-align: center;
  width: 100px;
  margin-left: 50px;
}
@media screen and (max-width: 786px) {
.nav {
    width: 100%;
    height: 50px;
    line-height: 50px;
    margin: 0 auto;
}
.nav ul li {
      width: 32%;
      margin: 0;
}
}



